import React from 'react'
import style from './post.module.css'

const Post = (props) => {
  

    return (
        <div className={style.item}>
            <img src='data:image/png;base64,' />
            {props.massage}
            {props.name}
            <div>
                <span>Liked </span> {props.likesCount}
            </div>
        </div>
    )
}
export default Post